// general and specific styles for elements that 
//  appear in the <main> section of the app.

// Display section tv shows
.displaySection {
    width: 100%;

    .displayInner {
        overflow: scroll;

        max-width: 100%;
        height: auto;

        .displayAllFilteredTvShows {
            display: flex;

            li {
                margin: 20px;

                button {
                    display: flex;
                    flex-direction: column;

                    width: 300px;
                    padding: 30px;

                    align-items: center;
                }
            }
        }
    }
}

@keyframes slide {
    from { background-position: 0 0; }
    to { background-position: -400px 0; }
}

header {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 100vh;

    color: $primaryBackgroundColor;
    font-size: 4rem;
    text-align: center;

    background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), url(../asset/tvShowCollage.png);
    background-size: cover;
    animation: slide 25s linear infinite;
    
    h1 {
        text-shadow: 5px 5px $secondaryColor;
    }
}

select {
    // margin: 10px 0 10px 30px;
    margin: 0 auto;
    padding: 2px;
    border: 3px solid $primaryBackgroundColor;
    border-radius: 6px;

    box-shadow: $secondaryColor 0 0 4px;
    font-size: 2.8rem;
    color: $primaryBackgroundColor;

    background: $secondaryColor;
    -moz-appearance: none;
	-webkit-appearance: none;
    appearance: none;
    background-image: url(../asset/arrowIcon.png);
	background-repeat: no-repeat;
	background-position: 95% center;
}

.dropdownGenre, .dropdownDays {
    display: flex;
    justify-content: center;
}

select:hover,
select:focus {
    border: 3px solid $secondaryColor;
}